.way {
  padding: 100px 0 100px;
  background-image: url("../../img/way-bg.jpg"), url("../../img/way-color_bg.jpg");
  background-repeat: no-repeat, no-repeat;
  background-position: top right, center left;
  background-size: cover, auto;
}

.way__right {
  width: 522px;
  margin-left: auto;
  font-family: $font-firasans;
  color: #484848;
}

.way__description {
  font-weight: 400;
  font-size: 15px;
  line-height: 170%;
  opacity: 0.87;
}

.way__important {
  display: block;
  width: 480px;
  font-size: 17px;
  line-height: 170%;
  font-weight: 700;
}

.way__list {
  padding: 0;
  margin-top: 24px;
  list-style-type: none;
  counter-reset: my-counter;
}

.way__item {
  margin-bottom: 45px;
  display: flex;
  align-items: center;
  position: relative;
  font-size: 15px;
  line-height: 140%;
  opacity: 0.87;

  &::before {
    content: counter(my-counter); 
    counter-increment: my-counter; 
    color: #FFC341;
    font-weight: 700; 
    font-size: 17px;
    line-height: 170%;
    margin-right: 24px; 
    width: 36px;
    height: 36px;
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.1);
    background-color: transparent;
    text-align: center;
    border-radius: 50%;
   }

   &::after {
     @include pseudo;
     width: 0px;
     height: 45px;
     border: 1px dashed #484848;
     bottom: -46px;
     left: 17px;
   }

   &:last-child {
     &::after {
       border: none;
     }
   }
}

.way__note {
  width: 520px;
  height: 42px;
  font-size: 15px;
  line-height: 140%;
  margin-left: 30px;
  opacity: 0.5;
  position: relative;

  &::before {
    @include pseudo;
    width: 4px;
    height: 42px;
    background: linear-gradient(108.42deg, #FFC341 20.46%, #FFD701 65.31%);
    top: 0;
    left: -15px;
  }
}